import React from 'react';
import {
  HeaderBlock,
  HeaderSection,
  HeaderSectionText,
  HeaderHalfSection
} from './styles';
import Nav from '../Nav';

const AppHeader = () => {
  return (
    <HeaderBlock>
      <HeaderSection>
        <HeaderHalfSection></HeaderHalfSection>
        <HeaderHalfSection></HeaderHalfSection>
      </HeaderSection>
      <HeaderSection>
        <Nav />
      </HeaderSection>
    </HeaderBlock>
  );
};

export default AppHeader;
